import React from 'react';
import { Switch, Route } from 'react-router-dom';
import LandingPage from './landingpage';
import About from './CS428_Projects';
import Project2 from './CS428_Project2';
import Contact from './contact';
import Projects from './projects';
import Resume from './resume';
import Student from './Student_choice';

const Main = () => (
    <Switch>
        <Route exact path="/" component = {LandingPage} />
        <Route path="/CS428_Projects" component = {About} />
        <Route path="/CS428_Project2" component = {Project2}/>
        <Route path="/Student_choice" component = {Student}/>
        <Route path="/contact" component = {Contact} />
        <Route path="/resume" component = {Resume} />
        <Route path="/projects" component = {Projects} />
        <Route path="*" component={LandingPage} />

    </Switch>

);

export default Main;
